<template>
  <SplitScreenSignIn/>
</template>

<script>
import axios from 'axios';
import SplitScreenSignIn from './components/SplitScreenSignIn.vue'


let _integrationApiBaseUrl;
let _apiBaseUrl;

// eslint-disable-next-line no-undef
if (weeconnectpayVueData.gitpodBackendWorkspaceUrl) {
  // eslint-disable-next-line no-undef
  _apiBaseUrl = weeconnectpayVueData.gitpodBackendWorkspaceUrl;
  // eslint-disable-next-line no-undef
  _integrationApiBaseUrl = weeconnectpayVueData.pluginUrl;
} else if (process.env.NODE_ENV === 'development') {
  _apiBaseUrl = 'https://weeconnect-api.test';
  _integrationApiBaseUrl = 'https://weeconnect-wp.test';
} else {
  // We import it from WP localize script.
  // eslint-disable-next-line no-undef
  _integrationApiBaseUrl = weeconnectpayVueData.pluginUrl;
  _apiBaseUrl = 'https://apidev.weeconnectpay.com';
}
const integrationApiBaseUrl = _integrationApiBaseUrl;
const apiBaseUrl = _apiBaseUrl;


const weeconnectpayApi = axios.create({
  baseURL: apiBaseUrl,
  headers: {
    Accept: 'application/json',
    Authorization: 'bearer ' + localStorage.getItem("authToken")
  }
})

const weeconnectpayPublicApi = axios.create({
  baseURL: apiBaseUrl,
  header: {Accept: 'application/json'}
})

const pluginApi = axios.create({
  baseURL: integrationApiBaseUrl,
  header: {Accept: 'application/json'}
})


export default {
  name: 'App',
  components: {
    //HelloWorld,
    //SimpleCardSignIn,
    SplitScreenSignIn
  },
  provide: {
    httpApi: weeconnectpayApi,
    httpPublicApi: weeconnectpayPublicApi,
    httpIntegrationApi: pluginApi,
  },
}
</script>

<style>
#weeconnectpay-app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>
